<template>
	<div id="music-list">
		<searchbar></searchbar>
		<div class="loading" v-if="loading">
			<p>loading...</p>
		</div>
		<ul v-if="!loading">
			<li class="music-item"
				v-for="(item, index) in musicList.songlist"
				@click="musicSelect(item, index)"
				>
				<div class="music-info">
					<div class="music-name">
						{{item.name}}
					</div>
					<div class="music-singer">
						<span v-for="(sub, index) in item.singer">
							{{sub.name}}
						</span>
						<span>-</span>
						<span v-for="(sub, index) in item">
							{{sub.title}}
						</span>
						<span>{{item.subtitle}}</span>
					</div>
				</div>
			</li>
		</ul>
	</div>
</template>
<script>
import searchbar from './searchbar.vue'
import { mapGetters } from 'vuex'
export default {
	name: "musiclist",
	data () {
		return {
			musicList:[],
			loading: true
		}
	},
	components: {
		searchbar
	},
	Created () {	
		this.$store.dispatch('getCDList', this.$route.params.id).then((res) => {
			this.musicList = res.data.cdlist[0]
			this.loading = false
		})	
		document.body.scrollTop = 0
	},
	beforeRouteLeave (to, from, next) {
		this.loading = true
		next()
	},
	//loadingtruecreated
	//loadingfalse
	activated () {
		this.$store.dispatch('getCDList', this.$route.params.id).then((res) => {
			this.musicList = res.data.cdlist[0]
			this.loading = false
		})
		document.body.scrollTop = 0
	},
	//activated keep-alive
	methods: {
		previous (){
			this.$router.go(-1)
		},
		musicSelect(item, index){
			//state
			this.$store.state.nowPlaying = item
			this.$store.state.nowIndex = index
			this.$store.state.playList = this.musicList.songlist
			this.$store.commit('play')
			this.$store.dispatch('albummid' ,item.mid)
		}
	}
}
</script>
<style lang="css" scoped>
.loading {
	display: flex;
	align-items: center;
	width: 100%;
	height: 100vh
}
.loading p {
	margin: 0 auto;
	padding-bottom: 39%;
}
#music-list {
	background: #fff;
}
#music-list .music-item{
	margin: 5px 0 0 5px;
}
#music-list li:nth-child(2n){
	background: gainsboro
}
.music-info {
	font-size: 14px;
}
.music-singer {
	font-size: 5px;
}
</style>


